<template>
  <v-container fluid>
    <v-card>
      <v-card-title>
        <h2>Check my wallet</h2>
      </v-card-title>
      <v-card-text>
        <v-text-field
          label="BTC Address"
          v-model="address"
          
        />
        <v-btn color="red" dark :loading="isChecking" @click="check()">Check</v-btn>
        <br />
        <template v-if="result">
          <v-layout row wrap>
            <v-flex xs12 class="text-xs-center">
              <h2>Satoshi: {{result.satoshi}}</h2>
              <v-tooltip bottom v-if="result.satoshi < 1000">
                <v-btn slot="activator" color="success" disabled>Withdraw</v-btn>
                <span>Withdraw amount should be equal or greater than 1000 satoshi.</span>
              </v-tooltip>
              <v-btn color="success" v-else @click="withdraw()" :loading="isWithrawing">Withdraw</v-btn>
            </v-flex>
            <v-flex xs12 class="mt-5">
              <h2 class="text-xs-center">TRANSACTION HISTORY</h2>
              <v-data-table
                :headers="headers"
                :items="items"
                hide-actions
                class="elevation-1"
              >
                <template slot="items" slot-scope="props">
                  <td>{{props.item.date}}</td>
                  <td>{{props.item.stats | status}}</td>
                  <td>{{props.item.amount}}</td>
                </template>
              </v-data-table>
              <p class="subheading mt-5">Note: The payment is processed and then sent to your wallet no longer than 7 business days after requesting it.</p>
            </v-flex>
          </v-layout>
        </template>
      </v-card-text>
    </v-card>
    <v-snackbar
      :timeout="3000"
      top
      v-model="snackbar"
    >
      {{snackbarText}}
      <v-btn flat color="pink" @click.native="snackbar = false">Close</v-btn>
    </v-snackbar>
  </v-container>
</template>

<script>
export default {
  data: () => ({
    address: '',
    isChecking: false,
    result: null,
    headers: [
      { text: 'Date', sortable: false, value: 'created' },
      { text: 'Status', sortable: false, value: 'stats' },
      { text: 'Amount', sortable: true, value: 'amount' }
    ],
    snackbar: false,
    snackbarText: '',
    items: [],
    isWithrawing: false
  }),
  methods: {
    check () {
      this.isChecking = true
      axios.get(`/wallet/check?address=${this.address}`).then((res) => {
        let result = res.data
        if (result.success) {
          this.result = result.message
          this.items = result.message.histories.reverse()
        } else {
          this.snackbar = true
          this.snackbarText = result.message
        }
        this.isChecking = false
      })
    },
    withdraw () {
      this.isWithrawing = true
      axios.get(`/wallet/withdraw?address=${this.address}`).then((res) => {
        this.isWithrawing = false
        let result = res.data
        this.snackbar = true
        if (result.success) {
          this.snackbarText = 'Success'
          this.result.satoshi = 0
          this.result.histories.push(result.message)
        } else {
          this.snackbarText = 'Error'
        }
      })
    }
  },
  filters: {
    status (val) {
      return (val === 0) ? 'Pending' : 'Complete'
    }
  }
}
</script>

